import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
import { FacebookService, InitParams } from 'ngx-facebook';
import { Subscription } from 'rxjs';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css',
]
})
export class AppComponent{
  title = 'capriclient';
  
}
$(document).ready(function () {
  $('#menu-toggle').on('click', function () {
      $('#wrapper').toggleClass('toggled');
  });

 const Accordion = function(el, multiple) {
     this.el = el || {};
     // more then one submenu open?
     this.multiple = multiple || false;

     const dropdownlink = this.el.find('.dropdownlink');
     dropdownlink.on('click', { el: this.el, multiple: this.multiple }, this.dropdown);
   };

   Accordion.prototype.dropdown = function(e) {
     const $el = e.data.el,
         $this = $(this),
         // this is the ul.submenuItems
         $next = $this.next();

     $next.slideToggle();
     $this.parent().toggleClass('open');

     if (!e.data.multiple) {
       // show only one menu at the same time
       $el.find('.submenu-level-1').not($next).slideUp().parent().removeClass('open');
     }
   };

   const accordion = new Accordion($('.sidebar-nav'), false);
});

