@Component({
  selector: 'app-covid',
  templateUrl: './covid.component.html',
  styleUrls: ['./covid.component.css']
})
export class CovidComponent implements OnInit {
  //apiUrl = window["ConfigApiBaseurl"];
  apiUrl="http://localhost:8088/"
  getallData: Array<any> = [];
  allCovidData: any=[]
  getSingleData = {};
  covidobj: any;
  alldata: any;
  covidid:any;
  constructor( private myRoute: Router,private router: Router,private http: HttpClient, private modal: NgbModal,) { }
  exTable: Tabulator;
  filterParam: string = "";
   printIcon = function(cell, formatterParams, onRendered){ //plain text value
    return "<i class='fa fa-edit'></i>";
};
addIcon = function(cell, formatterParams, onRendered){ //plain text value
  return "<i class='fa fa-fw fa-save'></i>";
};
 openModalcellClick = function(e, cell){
  this.covidid = cell.getRow().getData().id;
  this.getCovidById(this.covidid)
  // this.getSingleSystemData(cell.getRow().getData().customercnic);
   //this.getSystemData();
  //do something
};
savecellClick = function(e, cell){

   this.covidsummary[0]={
    country:cell.getRow().getData().country,
    deathratio:cell.getRow().getData().deathratio,
    recoveryratio: cell.getRow().getData().recoveryratio,
    totaldeaths: cell.getRow().getData().totaldeaths,
    totalrecovered: cell.getRow().getData().totalrecovered,
    totalcases: cell.getRow().getData().totalcases,
    savedon: new Date().toLocaleString(),
    updatedon:''

  }
   this.addCovid(this.covidsummary[0])

};


  CovidSummary_def = [
      { title: "Country", field: "country",width: 248,headerFilter:true, headerFilterPlaceholder:"Search City" },
      { title: "Total Cases", field: "totalcases",width: 200 },
      { title: "Total Deaths", field: "totaldeaths",width: 247 },
      { title: "Total Recovered", field: "totalrecovered",width: 278 },
      { title: "Death Ratio", field: "deathratio",width: 248 },
      { title: "Recovery Ratio", field: "recoveryratio",width: 270 },
      { formatter:this.addIcon,width:40, align:"center",cellClick:this.savecellClick.bind(this),tooltip:"Save Data"},
      // { formatter:this.printIcon,width:40, align:"center",cellClick:this.openModalcellClick.bind(this),tooltip:"Edit Data"},

    ];
    CovidDBSummary_def = [
      { title: "ID", field: "id",width: 150 },
      { title: "Country", field: "country",width: 248,headerFilter:true, headerFilterPlaceholder:"Search City" },
      { title: "Total Cases", field: "totalcases",width: 200 },
      { title: "Total Deaths", field: "totaldeaths",width: 247 },
      { title: "Total Recovered", field: "totalrecovered",width: 248 },
      { title: "Death Ratio", field: "deathratio",width: 248 },
      { title: "Recovery Ratio", field: "recoveryratio",width: 240 },
     // { formatter:this.addIcon,width:40, align:"center",cellClick:this.savecellClick.bind(this),tooltip:"Save Data"},
      { formatter:this.printIcon,width:40, align:"center",cellClick:this.openModalcellClick.bind(this),tooltip:"Edit Data"},

    ];
    covidsummary = [{
      id:undefined,
      country: undefined,
      totalcases: undefined,
      totaldeaths: undefined,
      totalrecovered: undefined,
      deathratio: undefined,
      recoveryratio:undefined,
      savedon:undefined,
      updatedon:undefined
  
    }]
    covidDBsummary = {
      id:undefined,
      country: undefined,
      totalcases: undefined,
      totaldeaths: undefined,
      totalrecovered: undefined,
      deathratio: undefined,
      recoveryratio:undefined,
      savedon:undefined,
      updatedon:undefined
  
    }

  ngOnInit() {
    if(sessionStorage.getItem('LoggedInUser')!="admin"){
      this.router.navigateByUrl('/login');
    }
    else{
    this.getSystemData();
    }
  }
  @ViewChild('modalContent') modalContent: TemplateRef<any>;
 
  bindCovidSummary(tabledata){
    this.exTable = new Tabulator("#ex-table-div",{
      height : 700,
      data: tabledata,
      layout: "fitDataFill",
      columns: this.CovidSummary_def,
      pagination:"local",
      rowClick:function(e, row){
        //e - the click event object
        //row - row component
  
        var data = row.getData(); //get row data
        console.log("row clicked");
        console.log(data);
    },
  
    
     // movableColumns: true,
    });
  }
  bindCovidDBSummary(tabledata){
    this.exTable = new Tabulator("#ex-covidDB-div",{
      height : 700,
      data: tabledata,
      layout: "fitDataFill",
      columns: this.CovidDBSummary_def,
      pagination:"local",
      rowClick:function(e, row){
        //e - the click event object
        //row - row component
  
        var data = row.getData(); //get row data
        console.log("row clicked");
        console.log(data);
    },
    
     // movableColumns: true,
    });
  }
  getSystemData() {

    swal({
     title: 'Processing', text: 'please wait...', onOpen: () => {
       swal.showLoading()
     }
   });

    let url = 'https://cors-anywhere.herokuapp.com/https://api.quarantine.country/';
    let headers = new HttpHeaders({ 'Content-Type': 'text' });
    headers.append('Access-Control-Allow-Origin','https://cors-anywhere.herokuapp.com/https://api.quarantine.country/')
    headers.append('X-Content-Type-Options','nosniff')
    headers.append('X-XSS-Protection','1; mode=block')
    headers.append('Content-Type','application/json')
    headers.append('Vary','Accept-Encoding')
    headers.append('Content-Type','application/json')
   
    const options = {
      headers: headers,
      
  };

    this.http.get(url,options).subscribe(
      (val) => {
      this.alldata = val;
      for(let i=0;i<191;i++){
        // swal({
        //   title: 'Processing', text: 'please wait...', onOpen: () => {
           
        //   }
        // });
        this.covidobj = Object.values(this.alldata.regions)[i]
    this.covidsummary[i]={
      id:'',
      country:Object.keys(this.alldata.regions)[i],
      deathratio:this.covidobj.death_ratio,
      recoveryratio: this.covidobj.recovery_ratio,
      totaldeaths: this.covidobj.deaths,
      totalrecovered: this.covidobj.recovered,
      totalcases: this.covidobj.total_cases,
      savedon:'',
      updatedon:''
    }
}
        console.log("this is covid arr")
        console.log(this.covidsummary)
      this.bindCovidSummary(this.covidsummary);
        
       swal.close();
       swal('records loaded!', 'success');
        
        //console.log("POST call successful value returned in body", val);
        
      },
      response => {
        swal({ type: 'error', title: 'Oops...', text: 'error loading data!', });
        //console.log("POST call in error", response);
      },
      () => {
        //console.log("The POST observable is now completed.");
      }
    );

    
  }
  getAllCovid() {
    this.covidsummary = []
    let url =this.apiUrl+'covid/getall';

    this.http.get(url).subscribe(
      (val) => {
        console.log(this.alldata)
      this.alldata = val;
     
      this.alldata.forEach(element => {
        this.covidDBsummary={
          id:element.id,
          country:element.country,
          deathratio:element.deathratio,
          recoveryratio: element.recoveryratio,
          totaldeaths: element.totaldeaths,
          totalrecovered: element.totalrecovered,
          totalcases: element.totalcases,
          savedon:element.savedon,
          updatedon:element.updatedon
        }
        this.covidsummary.push(this.covidDBsummary)

      });
  
  
    

        console.log("this is covid arr")
        console.log(this.covidsummary)
      this.bindCovidDBSummary(this.covidsummary);
        
       // swal.close();
       // swal('records loaded!', 'success');
        
        //console.log("POST call successful value returned in body", val);
        
      },
      response => {
        swal({ type: 'error', title: 'Oops...', text: 'error loading data!', });
        //console.log("POST call in error", response);
      },
      () => {
        //console.log("The POST observable is now completed.");
      }
    );

    
  }
  refresh(){
    this.getSystemData();
  }
  cleardata() {
   this.getSingleData = {};
  }
  addCovid(coviddata) {
    swal({
      title: 'Processing', text: 'please wait...', onOpen: () => {
        swal.showLoading()
      }
    });

    let url = this.apiUrl + '/covid/add';
    let headers = new HttpHeaders({ 'Content-Type': 'application/json' });

    //console.log(JSON.stringify(body));
    this.http.post(url, coviddata, { headers: headers }).subscribe(
      (val) => {
        swal.close();
        swal('Saved!', 'success');
        this.restAll();
        //console.log("POST call successful value returned in body", val);
        
      },
      response => {
        swal({ type: 'error', title: 'Oops...', text: 'Not saved succesfully!', });
        //console.log("POST call in error", response);
      },
      () => {
        //console.log("The POST observable is now completed.");
      }
    );
  }

  restAll(){
    this.getSingleData = {};
  }

  getCovidById(id) {

    swal({
      title: 'Processing', text: 'please wait...', onOpen: () => {
        swal.showLoading()
      }
    });

    let url = this.apiUrl + 'covid/getbyid';
    let params = new HttpParams();
    params = params.append('id',id);
    console.log(url)
    this.http.get(url, { params: params }).subscribe(
      (val) => {
        swal.close();
        this.getSingleData = val;
        this.modal.open(this.modalContent, { size: 'lg' });
      //  $('#editSystemDataModalBtn').click();
        // console.log(this.clcodeData)
      },
      response => {
        swal({ type: 'error', title: 'Oops...', text: 'error!', });
        //console.log("call in error", response);
      },
      () => {
        // console.log("The observable is now completed.");
      }
    );
  
  }
  editSystemData() {
    console.log(this.getSingleData);

    swal({
      title: 'Processing', text: 'please wait...', onOpen: () => {
        swal.showLoading()
      }
    });

    let url = this.apiUrl + 'covid/update/';
    let headers = new HttpHeaders({ 'Content-Type': 'application/json' });
    let params = new HttpParams().set('id', this.covidid)

    //console.log(JSON.stringify(body));
    this.http.put(url, this.getSingleData, { headers: headers,params:params },).subscribe(
      (val) => {
        this.getAllCovid()
        console.log(val);
      
        swal.close();
        swal('Saved!', 'success');
      
        this.modal.dismissAll();
        
        //console.log("POST call successful value returned in body", val);
      },
      response => {
        swal({ type: 'error', title: 'Oops...', text: 'Not saved succesfully!', });
        //console.log("POST call in error", response);
      },
      () => {
        
        //console.log("The POST observable is now completed.");
      }
    );
  }

}
